import React, { useEffect, useState } from 'react';
import './Titlebar.css';

import hamburger from '../../images/misc/hamburger.svg'
import hamburger_white from '../../images/misc/hamburger-white.svg'
import github_grey from '../../images/misc/github-grey.svg'
import email_grey from '../../images/misc/email-grey.svg'
import linkedin_grey from '../../images/misc/linkedin-grey.svg'
import github_white from '../../images/misc/github-white.svg'
import email_white from '../../images/misc/email-white.svg'
import linkedin_white from '../../images/misc/linkedin-white.svg'
import github_black from '../../images/misc/github-black.svg'
import email_black from '../../images/misc/email-black.svg'
import linkedin_black from '../../images/misc/linkedin-black.svg'

import Dropdown from 'react-bootstrap/Dropdown'

class Titlebar extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			showMenu: false,
		}
	}

	showMenu = () => {
		this.setState({
			showMenu: !this.state.showMenu
		})
	}

	handleResize = () => {
		if (window.innerWidth > 700) {
			this.setState({
				showMenu: false,
			})
		}
	}

	render() {
		window.addEventListener("resize", this.handleResize);

		let bgColor = this.props.bg ? {backgroundColor: this.props.bg, color: 'white'} : {}
		let hamburgerColor = {backgroundColor: 'transparent', color: 'black'}
		const titlebarCenter = this.props.mainPage ? {visibility: 'visible'} : {visibility: 'hidden'}
		const icons = [github_white, email_white, linkedin_white]
		const hamburgerIcons = (this.props.bg !== "transparent") ? [github_black, email_black, linkedin_black] : [github_white, email_white, linkedin_white]
		let titleBg = (this.props.bg === "transparent") ? {color: this.props.color || 'black', backgroundColor: "rgba(255, 255, 255, 0.5)"} : {color: this.props.color || 'white'}
		if (this.props.mainPage) {
			titleBg.color = 'black'
			bgColor.backgroundColor = "transparent"
		}

		if (this.props.bg === "transparent") {
			hamburgerColor.backgroundColor = 'black';
			hamburgerColor.color = 'white'
		} else {
			titleBg.backgroundColor = "transparent"
		}

	  return (
	  	<div className="titlebar-container">
	  	<div className="hamburger-menu" style={this.state.showMenu ? {display: 'flex', ...hamburgerColor} : {display: 'none'}}>
	  		<div className="hamburger-menu__text"><a href='/' className='link'>Work</a></div>
	  		<div className="contact-buttons">
		    	<div className="hamburger-menu__text nav-button"><a href = "//github.com/uuberoy" style={{height:"30px"}}><img src={hamburgerIcons[0]} height={"30px"}/></a></div>
		    	<div className="hamburger-menu__text nav-button"><a href = "mailto: uuberoy@princeton.edu" style={{height:"30px"}}><img src={hamburgerIcons[1]} height={"30px"}/></a></div>
	  			<div className="hamburger-menu__text nav-button"><a href = "//linkedin.com/in/urvashi-uberoy-951b34136/" style={{height:"30px"}}><img src={hamburgerIcons[2]} height={"30px"}/></a></div>
		  	</div>
	  		<div className="hamburger-menu__text"><a href='/about' className='link'>About</a></div>
	  	</div>
	  	<div className="titlebar" style={this.state.showMenu ? {top: '32px', ...bgColor} : {top: '0px', ...bgColor}}>
	  		<div className="titlebar-left">
	  			<div className="nav-item" style={{ marginLeft: '5vw', ...bgColor, ...titleBg}}><a href='/code' className='link'>Code</a></div>
	  			<div className="nav-item" style={{...bgColor,...titleBg}}><a href='/design' className='link'>Design</a></div>
	  			<div className="nav-item" style={{...bgColor,...titleBg}}><a href='/' className='link'>All</a></div>
	  		</div>
		  	<div style={{...titlebarCenter}} className="titlebar-center">
			    <div className="title" style={this.props.bg ? {color: 'white', ...titleBg} : {color: 'black',...titleBg}}><a href='/about' className='link'>
			    	URVASHI UBEROY
			    </a></div>
			    <div className="byline">
			    	Coder | Designer
			    </div>
		    </div>
		    <div className="titlebar-right">
		    	<div className="nav-item" style={{...bgColor, ...titleBg}}><a href='/about' className='link'>About</a></div>
		    	<div className="contact-buttons">
			    	<div className="nav-item nav-button"><a href = "//github.com/uuberoy" style={{height:"30px"}}><img src={icons[0]} height={"30px"}/></a></div>
			    	<div className="nav-item nav-button"><a href = "mailto: uuberoy@princeton.edu" style={{height:"30px"}}><img src={icons[1]} height={"30px"}/></a></div>
		  			<div className="nav-item nav-button"><a href = "//linkedin.com/in/urvashi-uberoy-951b34136/" style={{height:"30px"}}><img src={icons[2]} height={"30px"}/></a></div>
		  		</div>
		    	<div className="hamburger-container">
		    		<img src={this.props.bg ? hamburger_white : hamburger} className="hamburger" onClick={this.showMenu}/>
		    	</div>
		    </div>
	    </div>
	    </div>
	  );
	}
}

export default Titlebar;